<template>
  <div class="title" v-on:wheel.passive="wheelson" v-touch:swipe.up="switcher">
    <vue-particles
      color="#000000"
      :particleOpacity="1.0"
      :particlesNumber="75"
      shapeType="circle"
      :particleSize="7"
      linesColor="#FF85AC"
      :linesWidth="2"
      :lineLinked="true"
      :lineOpacity="1.0"
      :linesDistance="120"
      :moveSpeed="1"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="true"
      clickMode="push"
    ></vue-particles>
    <div id="text">
      <div id="first">Vishnu</div>
      <div id="last">Rengaraj</div>
      <div id="subtitle">{{ whois[0] }}</div>
    </div>

    <button id="testButton" v-on:click="toggle">Toggle</button>

    <button id="learn" v-on:click="toggle">
      <div class="arrow">
        <span></span>
        <span></span>
        <span></span>
      </div>
    </button>

    <div id="test1" v-bind:class="{ activeT1: isActive }"></div>
    <div id="test2" v-bind:class="{ activeT2: isActive }"></div>
    <div id="test3" v-bind:class="{ activeT3: isActive }"></div>
    <div id="test4" v-bind:class="{ activeT4: isActive }"></div>
  </div>
</template>

<script>
export default {
  name: "Title",

  data() {
    return {
      isActive: false,
      whois: [
        "Mechanical Engineer",
        "Computer Scientist",
        "Graduate Student",
        "Designer",
        "Stress Analyst",
        "Banana Lover"
      ]
    };
  },
  mounted() {
    window.setInterval(() => {
      this.pollPerson();
    }, 2500);
  },
  methods: {
    toggle: function() {
      this.isActive = !this.isActive;
      console.log(this.isActive);
    },

    wheelson: function(event) {
      if (event.deltaY > 0) {
        this.isActive = true;
        console.log(this.isActive);
      }
      if (event.deltaY < 0) {
        this.isActive = false;
        console.log(this.isActive);
      }
    },

    switcher: function() {
      this.isActive = true;
    },

    pollPerson() {
      const first = this.whois.shift();
      this.whois = this.whois.concat(first);
    }
  }
};
</script>

<style scoped>
.activeT1 {
  height: 100% !important;
}
.activeT2 {
  height: 100% !important;
}
.activeT3 {
  height: 100% !important;
}
.activeT4 {
  height: 100% !important;
}

#learn {
  background-color: transparent;
  border: none;
  box-shadow: transparent;
  position: absolute;
  left: 50%;
  transform: translateX(-50%) !important;

  bottom: 12vw;
  z-index: 2;
}
.arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.arrow span {
  display: block;
  width: 4vw;
  height: 4vw;
  border-bottom: 3px solid #fafafa;
  border-right: 3px solid #fafafa;
  transform: rotate(45deg);
  margin: -10px;
  animation: animate 2s infinite;
}
.arrow span:nth-child(2) {
  animation-delay: -0.2s;
}
.arrow span:nth-child(3) {
  animation-delay: -0.4s;
}
@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-20px, -20px);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(20px, 20px);
  }
}

#pull {
  background-color: #85ffd8;
  position: absolute;
  width: 10%;
  left: 50%;
  bottom: 0%;

  z-index: 7;
  height: 6%;
  transition: all 800ms ease-in-out; /*cubic-bezier(0.075, 0.82, 0.165, 1);*/
}

#testButton {
  position: absolute;
  right: 5%;
  top: 5%;
  z-index: 99;
}

#test1,
#test2,
#test3,
#test4 {
  overflow: hidden;
  position: absolute;
  z-index: 7;
  width: 100%;
  height: 0%;
  left: 0;
  bottom: 0;
  background-color: #3b434d;
  transition: all 800ms ease-in-out; /*cubic-bezier(0.075, 0.82, 0.165, 1);*/
}

#test2 {
  z-index: 8;
  /*height: 4%;*/
  background-color: #737980;
  transition-delay: 150ms;
}

#test3 {
  z-index: 9;
  /*height: 2%;*/
  background-color: #abaeb3;
  transition-delay: 300ms;
}

#test4 {
  z-index: 10;
  /*height: 0%;*/
  background-color: #fafafa;
  transition-delay: 450ms;
}

#particles-js {
  background-color: #1f2833;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

#text {
  position: absolute;
  top: 20%;
  left: 20%;

  /*transform: translate(-75%, -50%);*/

  height: 40vh;
  z-index: 2;
}

#first,
#last,
#subtitle {
  text-align: left;
  letter-spacing: 7px;
  font-size: 9vw;
  color: #fafafa;
  padding: 5vw;
  padding-left: 0;
  font-weight: 450;
  display: block;
  z-index: 2;
  /*
  background-color: black;*/
}

#subtitle {
  font-size: 4.5vw;
  padding: 2.5vw;
  padding-left: 0;
}

@media screen and (min-width: 1000px) {
  #first,
  #last {
    font-size: 90px;
    padding: 50px;
    padding-left: 0;
  }

  #subtitle {
    font-size: 45px;
    padding: 25px;
    padding-left: 0;
  }

  #learn {
    bottom: 120px;
  }


  .arrow span {
    width: 30px;
    height: 30px;
  }
  


}
</style>
